<script setup>
import { ref } from 'vue'
import Button from './UI/Button.vue'

const emit = defineEmits(['next'])
const isFlipped = ref([false, false, false])
const isAllFlipped = ref(false)
const isClicked = ref([false, false, false])

const checkAllClicked = () => {
	isAllFlipped.value = isClicked.value.every(clicked => clicked)
}

const flipCard = index => {
	isFlipped.value = isFlipped.value.map((flip, i) =>
		i === index ? !flip : flip
	)
	isClicked.value[index] = true
	checkAllClicked()
}
</script>

<template>
	<div class="container">
		<div class="container__text">
			<h2>
				Давайте определим, <br />
				какой вид договора подходит для студента – практиканта. <br />После того
				как изучите виды договоров, сможете перейти дальше.
			</h2>
		</div>

		<div class="container__cards">
			<!-- Первая карточка -->
			<div class="card" :class="{ flipped: isFlipped[0] }" @click="flipCard(0)">
				<div class="card__face card__face--front">
					<h3>УЧЕНИЧЕСКИЙ ДОГОВОР</h3>
				</div>
				<div class="card__face card__face--back">
					<p>
						Ученический договор является дополнительным к трудовому договору.
						Составляется с условием компенсации организации затрат на обучение.
						В ученической договор можно внести другие условия, не противоречащие
						законодательству и не ущемляющие права учащегося. Такие требования
						установлены в ст. 199, 200, 203, 204, 207 ТК РФ.
					</p>
				</div>
			</div>

			<div class="card" :class="{ flipped: isFlipped[1] }" @click="flipCard(1)">
				<div class="card__face card__face--front">
					<h3>ТРУДОВОЙ ДОГОВОР</h3>
				</div>
				<div class="card__face card__face--back">
					<p>
						В период прохождения практики студент может зачисляться на вакантную
						должность, если работа соответствует требованиям программы
						производственной практики. Такие отношения квалифицируются как
						трудовые. Договор оформляется в общем порядке.
						<span style="color: red"
							>Со студентом может быть заключен срочный трудовой договор</span
						>
					</p>
				</div>
			</div>

			<div class="card" :class="{ flipped: isFlipped[2] }" @click="flipCard(2)">
				<div class="card__face card__face--front">
					<h3>ОТСУТСТВИЕ ТРУДОВОГО ДОГОВОРА</h3>
				</div>
				<div class="card__face card__face--back">
					<p>
						Если студент в процессе практики только обучается, ввиду отсутствия
						исполнения им трудовых функций, трудовой договор с ним не
						заключается. При отсутствии трудового договора перед практикантом не
						могут быть поставлены задачи, связанные с трудовой деятельностью.
					</p>
				</div>
			</div>
		</div>

		<Transition mode="out-in"
			><div v-if="isAllFlipped" class="container__btn">
				<Button background-color="#92d050" @click="$emit('next')"
					>ПЕРЕЙТИ ДАЛЬШЕ</Button
				>
			</div></Transition
		>
	</div>
</template>

<style scoped>
.container {
	margin: 0 1vw;
}

.container__text h2 {
	text-align: center;
	font-size: 1.4em;
}

.container__cards {
	display: flex;
	flex-flow: column nowrap;
	justify-content: center;
	align-items: center;
	margin-top: 2vw;
	gap: 2vw;
}

.card {
	width: 74vw;
	height: 25vw;
	position: relative;
	cursor: pointer;
	transform-style: preserve-3d;
	transition: transform 0.5s;
}

.card.flipped {
	transform: rotateY(180deg);
}

.card__face {
	position: absolute;
	width: 100%;
	height: 100%;
	backface-visibility: hidden;
	display: flex;
	justify-content: center;
	align-items: center;
	border: 1px solid black;
	border-radius: 1vw;
	padding: 1vw;
}

.card__face--front {
	background-color: #ededed;
	color: #333;
}

.card__face--back {
	background-color: #333;
	color: #fff;
	transform: rotateY(180deg);
}

.card__face h3 {
	font-size: 2em;
	text-align: center;
}

.card__face p {
	font-size: 1em;
	padding: 1vw;
	text-align: justify;
}

.container__btn {
	margin-top: 3vw;
	display: flex;
	justify-content: center;
}
</style>
