<script setup>
import { ref } from 'vue'
import Button from './UI/Button.vue'

const currentPage = ref(1)
const countFails = ref(0)

// Массив для хранения выделенных элементов
const highlightedElements = ref([])

// const incrementPage = () => {
// 	if (currentPage.value < 4) currentPage.value += 1
// }

// const decrementPage = () => {
// 	if (currentPage.value > 1) currentPage.value -= 1
// }

const animationDirection = ref('slide-forward') // 'slide-forward' или 'slide-backward'

const incrementPage = () => {
	if (currentPage.value < 4) {
		animationDirection.value = 'slide-forward'
		currentPage.value += 1
	}
}

const decrementPage = () => {
	if (currentPage.value > 1) {
		animationDirection.value = 'slide-backward'
		currentPage.value -= 1
	}
}

const checkUserAnswer = e => {
	const elementId = e.target.id // Уникальный идентификатор элемента

	// Проверяем, был ли элемент уже выделен
	if (!highlightedElements.value.includes(elementId)) {
		e.target.style.color = 'red' // Устанавливаем цвет
		highlightedElements.value.push(elementId) // Добавляем элемент в массив
		countFails.value += 1
	}
}

const restart = () => {
	location.reload()
}
</script>

<template>
	<div class="container">
		<div class="container__task">
			К вам пришел устраиваться студент в возрасте 17-ти лет. Вы из своей
			системы взяли готовый экземпляр трудового договора и заполняя его в
			спешке, допустили ошибки. Перед вами четыре рисунка с выпиской из
			трудового договора. Найдите в каждом из них ошибку. Чтобы найти ошибку,
			изучите фрагмент и наведите курсор мышки на место с ошибкой. Если вы
			правы, то это место станет кликабельным. Вперед!
		</div>

		<Transition :name="animationDirection" mode="out-in">
			<div :key="'page-' + currentPage" class="container__text">
				<div v-if="currentPage === 1">
					<div class="center m2">ТРУДОВОЙ ДОГОВОР № 01-Т</div>
					<div class="m2">Москва 01.00.20__</div>
					<div class="m2">
						ООО «Гербарий», именуемое в дальнейшем «Работодатель», в лице
						директора Цветкова Константина Владимировича, действующего на
						основании устава, с одной стороны и Студент, именуемый в дальнейшем
						«Работник», с другой стороны заключили настоящий трудовой договор о
						нижеследующем.
					</div>
					<div class="center m2">1. ОБЩИЕ ПОЛОЖЕНИЯ</div>
					<div class="m1">
						1.1. По настоящему трудовому договору Работодатель на период
						стажировки по специальности «Юрист» предоставляет Работнику работу
						по должности Помощника юриста, а Работник обязуется лично выполнять
						указанную работу в соответствии с условиями настоящего трудового
						договора.
					</div>
					<div class="m1">
						1.2. Работник принимается на работу в юридический отдел организации
						Работодателя
					</div>
					<div class="m1">
						1.3. Место работы Работника – ООО «Гербарий»,Москва.
					</div>
					<div class="m1">
						1.4. Работа у Работодателя является для Работника основной.
					</div>
					<div class="m1">
						1.5. Условия труда на рабочем месте Работника оптимальные – 1-й
						класс (по результатам специальной оценки условий труда от
						00.00.20__).
					</div>
					<div class="m1">
						1.6. Настоящий трудовой договор заключен по соглашению сторон на
						период практики в форме стажировки с 01 __ 20__ года
						<span
							id="error1"
							class="pointer"
							@click="checkUserAnswer"
							:style="{
								color: highlightedElements.includes('error1') ? 'red' : '',
							}"
							>бессрочно, согласно абзацу 9 части 2 статьи 59 Трудового
							кодекса.</span
						>
					</div>
					<div class="m1">
						1.7. Настоящий трудовой договор вступает в силу со дня допущения
						Работника к работе, определяемого пунктом 1.8 договора.
					</div>
					<div class="m1">1.8. Дата начала работы – 01 __ 20__ года.</div>
					<div class="m1">
						1.9. Работнику устанавливается срок испытания продолжительностью две
						недели с целью проверки соответствия Работника поручаемой работе.
					</div>
					<Transition mode="out-in"
						><div v-if="highlightedElements.includes('error1')" class="hit">
							Верно, со студентом можно заключить только срочный ТД, согласно
							статье 59 ТК РФ
						</div></Transition
					>
				</div>
				<div v-else-if="currentPage === 2">
					<div class="m2">
						Работник имеет иные права, предусмотренные трудовым
						законодательством РФ, настоящим трудовым договором.
					</div>
					<div class="m1">2.2. Работник обязан:</div>
					<div class="m1">
						2.2.1. Добросовестно исполнять свои трудовые обязанности,
						возложенные на него настоящим трудовым договором, а также
						должностной инструкцией, выполнять установленные нормы труда.
					</div>
					<div class="m1">
						2.2.2. Соблюдать Правила трудового распорядка, действующие у
						Работодателя, требования по охране труда и обеспечению безопасности
						труда, иные локальные нормативные акты Работодателя, непосредственно
						связанные с трудовой деятельностью Работника, с которыми Работник
						был ознакомлен под подпись.
					</div>
					<div class="m1">2.2.3. Соблюдать трудовую дисциплину.</div>
					<div class="m1">
						2.2.4. Бережно относиться к имуществу Работодателя, в том числе
