						здоровью людей, сохранности имущества Работодателя, в том числе
						находящегося у Работодателя имущества третьих лиц, если Работодатель
						несет ответственность за сохранность этого имущества, имущества
						других работников.
					</div>
					<div class="m1">
						Работник обязан выполнять другие обязанности, предусмотренные
						трудовым законодательством РФ и настоящим трудовым договором
					</div>
					<div class="m1">
						<span
							id="error2"
							@click="checkUserAnswer"
