					</div>
					<div class="m1">
						– работник не может быть привлечен к сверхурочной работе, работе в
						выходные и нерабочие праздничные дни, а также в ночное время (с 22
						до 6 часов);
					</div>
					<div class="m1">
						– при увольнении работника по инициативе работодателя работодатель
						обязан получить согласие инспекции по труду и комиссии по делам
						несовершеннолетни
					</div>
					<Transition mode="out-in"
						><div v-if="highlightedElements.includes('error2')" class="hit">
							Верно, сотрудникам до 18 лет предоставляются гарантии, согласно
							статьям 96, 268 ТК РФ
						</div></Transition
					>
				</div>
				<div v-else-if="currentPage === 3">
					<div class="m2">5. РАБОЧЕЕ ВРЕМЯ И ВРЕМЯ ОТДЫХА</div>
					<div class="m1">
						5.1. Работнику устанавливается пятидневная рабочая неделя
						продолжительностью
						<span
							id="error3"
							@click="checkUserAnswer"
							:style="{
								color: highlightedElements.includes('error3') ? 'red' : '',
							}"
							>40 (сорок) часов.</span
						>
						Выходными днями являются суббота и воскресенье.
					</div>
					<div class="m1">
						5.2. Работнику предоставляется ежегодный основной оплачиваемый
						отпуск продолжительностью 28 календарных дней.
