						данного Работодателя. По соглашению сторон оплачиваемый отпуск
						Работнику может быть предоставлен и до истечения шести
						месяцев. Отпуск за второй и последующие годы работы может
						предоставляться в любое время рабочего года в соответствии с
						графиком отпусков.
					</div>
					<div class="m1">
						5.3. Работнику как студенту-очнику на основании его письменного
						заявления предоставляется отпуск без сохранения заработной платы для
						сдачи сессии или итоговых экзаменов.
					</div>
					<div class="m1">
						По семейным обстоятельствам и другим уважительным причинам Работнику
						на основании его письменного заявления может быть предоставлен
