/* Анимация перехода вперед */
.slide-forward-enter-active {
	animation: slideInForward 0.5s ease-out, fadeIn 0.5s ease-out;
}
.slide-forward-leave-active {
	animation: slideOutBackward 0.5s ease-out, fadeOut 0.5s ease-out;
}

/* Анимация перехода назад */
.slide-backward-enter-active {
	animation: slideInBackward 0.5s ease-out, fadeIn 0.5s ease-out;
}
.slide-backward-leave-active {
	animation: slideOutForward 0.5s ease-out, fadeOut 0.5s ease-out;
}

/* Общие анимации */
@keyframes slideInForward {
	from {
		transform: translateX(100%);
		opacity: 0;
	}
	to {
		transform: translateX(0);
		opacity: 1;
	}
}

@keyframes slideOutBackward {
	from {
		transform: translateX(0);
		opacity: 1;
	}
	to {
		transform: translateX(-100%);
		opacity: 0;
	}
}

@keyframes slideInBackward {
	from {
		transform: translateX(-100%);
		opacity: 0;
	}
	to {
		transform: translateX(0);
		opacity: 1;
	}
}

@keyframes slideOutForward {
	from {
		transform: translateX(0);
		opacity: 1;
	}
	to {
		transform: translateX(100%);
		opacity: 0;
	}
}

@keyframes fadeIn {
	from {
		opacity: 0;
	}
