<script setup>
import Button from './UI/Button.vue'
import { ref } from 'vue'

const emit = defineEmits(['next'])

const yOne = ref(false)
const yTwo = ref(false)
const yThree = ref(false)
const yFour = ref(false)
const yFive = ref(false)
const isUserClick = ref(false)
const isUserAlreadyCheck = ref(false)
const isCorrect = ref(false)

const visibleY = name => {
	if (isUserAlreadyCheck.value) return
	if (name === 'one') {
		if (yOne.value === false) yOne.value = true
		else yOne.value = false
	}
	if (name === 'two') {
		if (yTwo.value === false) yTwo.value = true
		else yTwo.value = false
	}
	if (name === 'three') {
		if (yThree.value === false) yThree.value = true
		else yThree.value = false
	}
	if (name === 'four') {
		if (yFour.value === false) yFour.value = true
		else yFour.value = false
	}
	if (name === 'five') {
		if (yFive.value === false) yFive.value = true
		else yFive.value = false
	}

	isUserClick.value = true
}

const checkUserAnswer = () => {
	isUserAlreadyCheck.value = true
	if (
		yOne.value &&
		yThree.value &&
		yFive.value &&
		!yTwo.value &&
		!yFour.value
	) {
		isCorrect.value = true
	} else {
		isCorrect.value = false
	}
}

const restart = () => {
	yOne.value = false
	yTwo.value = false
	yThree.value = false
	yFour.value = false
	yFive.value = false
	isUserClick.value = false
	isUserAlreadyCheck.value = false
	isCorrect.value = false
}

const next = () => {
	emit('next')
}
</script>

<template>
	<div class="container">
		<div class="container__task_text">
			<span>
				ДАВАЙТЕ ПРОВЕРИМ ВАШИ ЗНАНИЯ, ПО ПРИЕМУ СТУДЕНТА ПРАКТИКАНТА. <br />
				ВЫБЕРИТЕ ПРАВИЛЬНОЕ УТВЕРЖДЕНИЕ. ПОСТАВЬТЕ
				<img src="../assets/zelenaya_galka.png" alt="" />В СООТВЕТСТВУЮЩЕЕ ОКНО
			</span>
		</div>
		<div class="container__table">
			<div
				class="container__table_item cursor_pointer"
				@click="visibleY('one')"
			>
				<Transition mode="out-in">
					<img v-if="yOne" src="../assets/zelenaya_galka.png" alt="" />
				</Transition>
			</div>
			<div class="container__table_item">
				Практику курирует два руководителя: куратор учебного заведения и
				руководитель отдела, куда приходит студент
			</div>

			<div
				class="container__table_item cursor_pointer"
				@click="visibleY('two')"
			>
				<Transition mode="out-in">
					<img v-if="yTwo" src="../assets/zelenaya_galka.png" alt="" />
				</Transition>
			</div>
			<div class="container__table_item">
				Практику курирует руководитель отдела, куда приходит студент
			</div>

			<div
				class="container__table_item cursor_pointer"
				@click="visibleY('three')"
			>
				<Transition mode="out-in"
					><img v-if="yThree" src="../assets/zelenaya_galka.png" alt=""
				/></Transition>
			</div>
			<div class="container__table_item">
				По окончании практики, руководитель отдела предоставляет характеристику
				на студента
			</div>

			<div
				class="container__table_item cursor_pointer"
				@click="visibleY('four')"
			>
				<Transition mode="out-in">
					<img v-if="yFour" src="../assets/zelenaya_galka.png" alt=""
				/></Transition>
			</div>
			<div class="container__table_item">
				По окончании практики, куратор учебного заведения предоставляет
				характеристику на студента
			</div>
			<div
				class="container__table_item cursor_pointer"
				@click="visibleY('five')"
			>
				<Transition mode="out-in"
					><img v-if="yFive" src="../assets/zelenaya_galka.png" alt=""
				/></Transition>
			</div>
			<div class="container__table_item">
				Студента, во время практики можно принять на вакантную должность
			</div>
		</div>
		<div class="container__btn">
			<Transition mode="out-in">
				<div v-if="isUserClick && !isUserAlreadyCheck">
					<Button @click="checkUserAnswer" background-color="#f8cbac"
						>ПРОВЕРИТЬ</Button
					>
				</div>
				<div v-else-if="isUserAlreadyCheck">
					<Transition mode="out-in">
						<div v-if="isCorrect">
							<Button background-color="#92d050" @click="next"
								>ПЕРЕЙТИ ДАЛЬШЕ
							</Button>
						</div>
						<div v-else>
							<Button background-color="red" color="#fafafa" @click="restart"
								>Вы ошиблись! <br />
								Попробовать снова
							</Button>
						</div></Transition
					>
				</div>
			</Transition>
		</div>
	</div>
</template>

<style scoped>
.container {
	margin: 0 1vw;
}

.container__task_text {
	display: flex;
	flex-flow: column nowrap;
	justify-content: center;
	align-items: center;
	text-align: center;
	line-height: 140%;
	margin-bottom: 3vw;
	font-weight: 900;
}

.container__task_text img {
	width: 2vw;
}

.container__table {
	display: grid;
	grid-template-columns: 0.3fr 1fr;
	border: 1px solid black;
}

.container__table_item {
	padding: 2vw;
	display: flex;
	flex-flow: column nowrap;
	align-items: center;
	justify-content: center;
	border: 1px solid black;
	border-collapse: collapse;
	min-height: 10vw;
}

.cursor_pointer {
	cursor: pointer;
}

.container__table_item img {
	width: 5vw;
}

.container__btn {
	margin-top: 3vw;
	display: flex;
	justify-content: center;
}
</style>
