<script setup>
import Start from './components/Start.vue'
import StepOne from './components/StepOne.vue'
import StepTwo from './components/StepTwo.vue'
import StepThree from './components/StepThree.vue'
import { ref } from 'vue'

const isStart = ref(false)
const isStepOne = ref(true)
const isStepTwo = ref(false)
const isStepTree = ref(false)

const stepTwo = () => {
	isStepOne.value = false
	isStepTwo.value = true
}

const stepThree = () => {
	isStepOne.value = false
	isStepTwo.value = false
	isStepTree.value = true
}
</script>

<template>
	<div class="container">
		<Transition mode="out-in">
			<div v-if="!isStart"><Start @start="isStart = true"></Start></div>
			<div v-else>
				<Transition mode="out-in">
					<div v-if="isStepOne"><StepOne @next="stepTwo"></StepOne></div>
					<div v-else-if="isStepTwo"><StepTwo @next="stepThree"></StepTwo></div>
					<div v-else><StepThree></StepThree></div>
				</Transition>
			</div>
		</Transition>
	</div>
	<footer></footer>
</template>

<style scoped>
footer {
	height: 4vw;
}
</style>
